body, html {
  overscroll-behavior: none;
  background-color: #000;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

*, :before, :after {
  box-sizing: border-box;
}

.flex-outer {
  justify-content: center;
  width: 100vw;
  height: 100vh;
  display: flex;
}

.flex-inner {
  writing-mode: vertical-rl;
  border: 1px dashed #000;
  align-items: center;
  width: 100%;
  max-width: 100%;
  height: 100vh;
  display: flex;
}

.canv {
  flex: 1 0 1px;
  padding-block-start: 100%;
  position: relative;
}

.canvasbox {
  writing-mode: horizontal-tb;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.title-text {
  color: #fff;
  padding: 5px;
  font: 2em Lato, sans-serif;
  position: absolute;
}

#webglCanvas {
  background-color: #0000;
  border: none;
  width: 100%;
  height: 100%;
  display: block;
}

@media (aspect-ratio <= 1) {
  .flex-inner {
    writing-mode: horizontal-tb;
  }
}
